import { useState } from "react";
import clsx from "clsx";
import {
  CompletedSVG,
  ConfiltSVG,
  OverdueAlarmSVG,
  WeatherSVG,
} from "../assets";

interface IActivity {
  title: string;
  details: string;
  time: string;
  user: string;
  status?: boolean;
  completed?: boolean;
  next?: boolean;
  weather?: boolean;
  statustype?: "overdue" | "not assigned" | "overdue-alert";
  confiltButtonShow?: boolean;
  length?: number | undefined;
  handleDivClick?: React.MouseEventHandler<HTMLDivElement>;
}

/**
 *
 * @param title is a Activity title
 * @param details is a Activity description
 * @param time is a Activity time
 * @param user is a Activity done by user
 * @param status is a Activity status done by user
 * @param statustype type of Activity status
 * @param completed Activity typed completed
 * @param next Activity should be next
 * @param weather Activity weather only
 * @param handleDivClick is a click event of the Activity
 */
export const Activity = ({
  title,
  details,
  time,
  user,
  status,
  statustype,
  completed,
  next,
  weather,
  confiltButtonShow,
  length,
}: IActivity) => {
  const [confiltState, setConfiltState] = useState<boolean>(false);
  const [optionState, setOptionState] = useState<boolean>(false);
  return (
    <>
      {completed && <CompletedSVG />}
      <div
        className={clsx(
          "Activity grow shrink basis-0 self-stretch p-4 border lg:border-zinc-300 flex-col justify-start items-start gap-3 inline-flex relative",
          completed && "bg-activity-completed bg-cover",
          next ? "bg-pink" : "bg-neutral-50",
          weather && "bg-pink"
        )}
      >
        <div className='ActivityContent self-stretch flex-col justify-start items-start lg:items-end gap-2 flex'>
          <div className='Detail self-stretch justify-start items-start gap-1 inline-flex'>
            <div className='Header grow shrink basis-0 flex-col justify-start items-start gap-1 inline-flex'>
              <div className='DescriptionTime justify-start items-start gap-1 inline-flex'>
                <div className='Time flex-col justify-start items-start gap-2.5 inline-flex'>
                  <div
                    className={clsx(
                      "002300 text-[10px] font-medium tracking-wide",
                      completed
                        ? "text-white"
                        : "lg:text-black text-neutral-400"
                    )}
                  >
                    {time}
                  </div>
                </div>
              </div>
              {!weather && (
                <div className='MachinePitch flex-col justify-start items-start gap-2.5 hidden lg:block'>
                  <div
                    className={clsx(
                      "P1 text-[10px] font-medium uppercase tracking-wide",
                      completed ? "text-white" : "text-neutral-400"
                    )}
                  >
                    {details}
                  </div>
                </div>
              )}
            </div>

            <div className=' z-20 relative'>
              {!(next || weather) && (
                <div
                  className={clsx(
                    "text-right text-[18px] font-medium hidden lg:block cursor-pointer",
                    completed ? "text-white" : "text-neutral-400"
                  )}
                  onClick={() => {
                    setOptionState(true);
                    // e.stopPropagation();
                  }}
                >
                  ⠇
                </div>
              )}

              <div
                className={clsx(
                  "Dropdown w-[164px]   bg-white shadow border border-neutral-200 flex-col justify-start items-start inline-flex origin-top-right top-[30px] absolute right-0 text-[16px] font-medium",
                  optionState ? "inline-flex" : "hidden"
                )}
              >
                <button
                  className='justify-start items-center px-2.5 py-1 min-h-[36px] flex hover:bg-gray-50 w-full'
                  onClick={() => setOptionState(false)}
                >
                  Reject (Unclaim)
                </button>
                <button
                  className='justify-start items-center px-2.5 py-1 min-h-[36px] flex hover:bg-gray-50 w-full'
                  onClick={() => setOptionState(false)}
                >
                  Complete
                </button>
              </div>
            </div>
          </div>
          {!weather && (
            <div className='TitleName w-full  h-[23px] justify-between items-start gap-2 flex'>
              <div className='Title justify-start items-baseline gap-6 flex'>
                <div
                  className={clsx(
                    "Mowing text-[16px] font-medium",
                    completed ? "text-white" : "text-black",
                    length && length > 3 && "max-w-[49px] truncate"
                  )}
                >
                  {title}
                </div>
                {!completed &&
                  status &&
                  (statustype == "overdue" ? (
                    <div className='LabelBadge px-3 py-1 bg-[#df1212] bg-opacity-20 rounded-[100px] justify-start items-start gap-2.5 flex'>
                      <div className='Overdue text-[#df1212] text-[12px] font-medium tracking-wide'>
                        overdue
                      </div>
                    </div>
                  ) : statustype == "overdue-alert" ? (
                    <div className='LabelBadge px-3 py-1 bg-[#df1212] bg-opacity-20 rounded-[100px] justify-start items-start gap-2.5 flex'>
                      <div className='Overdue text-[#df1212] text-[12px] font-medium tracking-wide'>
                        overdue
                      </div>
                      <div className='PendingIcon w-[16.91px] p-1 justify-center items-center flex'>
                        <OverdueAlarmSVG />
                      </div>
                    </div>
                  ) : (
                    <div className='LabelBadge px-3 py-1 bg-zinc-100 bg-opacity-20 rounded-[100px] justify-start items-start gap-2.5 flex'>
                      <div className='NotAssigned text-neutral-800 text-opacity-50 text-[12px] font-medium tracking-wide'>
                        not assigned
                      </div>
                    </div>
                  ))}
              </div>
              <div className='NameTag w-[23px] h-[23px] relative shrink-0'>
                <div className='Bg w-[23px] h-[23px] left-0 top-0 absolute opacity-60 bg-cyan-500 rounded-full' />
                <div className='Ap w-[23px] left-0 top-[4.62px] absolute text-center text-white text-[10px] font-semibold'>
                  {user}
                </div>
              </div>
            </div>
          )}
          {weather && (
            <div className='Title self-stretch justify-between items-start gap-[214px] inline-flex'>
              <WeatherSVG />
            </div>
          )}
        </div>

        <div
          className='group w-5 h-5 absolute top-1/2  -translate-y-1/2 right-[-29px] z-20 flex'
          onClick={() => setConfiltState(true)}
        >
          {confiltButtonShow && (
            <button
              className='IconExclamation '
              type='button'
              data-tooltip-target='tooltip-right'
              data-tooltip-placement='right'
            >
              <ConfiltSVG />
            </button>
          )}
          <span
            className={clsx(
              "absolute top-1/2 -translate-y-1/2 scale-0 transition-all  p-2 text-xs bg-white left-[25px] text-gray-800 text-[16px] font-medium",
              confiltState && "group-hover:scale-100 w-36"
            )}
            onClick={() => setConfiltState(false)}
          >
            Ignore conflict
          </span>
        </div>
      </div>
    </>
  );
};
